.input {
  width: 100%;
  padding: 12px 20px;
  /* margin: 8px; */
  box-sizing: border-box;
  /* border: none; */
  border: 2px solid #ccc;
  font-size: 20px;
  border-radius: 8px;
  margin-top: 30px;
  margin-bottom: 10px;
}
.header {
  text-align: center;
  font-size: 40px;
  margin-top: 30px;
}

.btn {
  background-color: #4caf50;
  color: white;
  padding: 20px 20px;
  margin: 8px 0;
  border: none;
  cursor: pointer;
  width: 100%;
  border-radius: 8px;
}

.error {
  color: red;
  font-size: 20px;
}

.success_text {
  text-align: center;
  font-size: 20px;
  margin-top: 30px;
  color: #4caf50;
}

.App {
  text-align: center;
  margin: 20px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
